import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import view from './view';
import styles from './styles/index.scss';
import {
	NOW_CARD_CLICKED,
	DECLARATIVE_ACTION,
	NOW_BUTTON_BARE_CLICKED,
	REGULAR_RESULT_TYPE,
	CLICK
} from './constants';

/**
 * Card component with a configurable interface ideal to display any type of search results.
 *
 * ```jsx
 * 	<sn-search-result-evam-card
 * 	  imageType={imageType}
 * 	  imageURL={imageURL}
 * 	  icon={icon}
 * 	  avatarURL={avatarURL}
 * 	  avatarName={avatarName}
 * 	  textHeaderLabelOne={textHeaderLabelOne}
 * 	  textHeaderLabelTwo={textHeaderLabelTwo}
 * 	  textHeaderLabelThree={textHeaderLabelThree}
 * 	  title={title}
 * 	  summary={summary}
 * 	  detailLabelType={detailLabelType}
 * 	  detailLabelSeparator={detailLabelSeparator}
 * 	  detailLabelOne={detailLabelOne}
 * 	  detailValueOne={detailValueOne}
 * 	  detailLabelTwo={detailLabelTwo}
 * 	  detailValueTwo={detailValueTwo}
 * 	  clickAction={clickAction}
 * 	  footerLinkAction={footerLinkAction}
 * 	  model={model}
 * 	/>
 * ```
 *
 * @seismicElement sn-search-result-evam-card
 * @summary Card component with a configurable interface ideal to display any type of search results.
 */
createCustomElement('sn-search-result-evam-card', {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * Defines the main title of the card.
		 * @type {string}
		 */
		title: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies what type of marker to display.
		 * @type ('image' | 'avatar' | 'icon')
		 */
		imageType: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies the image URL to display when the imageType is set to image.
		 * @type {string}
		 */
		imageURL: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies the name of the icon to display when the imageType is set to icon or alternatively, as a fallback icon when the imageType is set to image but there is no imageURL provided.
		 * @type {string}
		 */
		icon: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies the avatar URL to display when the imageType is set to avatar.
		 * @type {string}
		 */
		avatarURL: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies the avatar name to display when the imageType is set to avatar.
		 * @type {string}
		 */
		avatarName: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Defines the text displayed in the main content of the card.
		 * @type {string}
		 */
		summary: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies the type of label value pair to use.
		 * @type ('stacked' | 'inline')
		 */
		detailLabelType: {
			default: 'inline',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies the characters to use to separate the inline label value pairs.
		 * The separator will show if both label value pairs are specified.
		 * @type {string}
		 */
		detailLabelSeparator: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies the label text of the first label value pair.
		 * @type {string}
		 */
		detailLabelOne: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies the value text of the first label value pair.
		 * @type {string}
		 */
		detailValueOne: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies the label text of the second label value pair.
		 * @type {string}
		 */
		detailLabelTwo: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies the value text of the second label value pair.
		 * @type {string}
		 */
		detailValueTwo: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Defines the action metadata when the card is clicked.
		 * @type {object}
		 */
		clickAction: {
			default: {},
			schema: {
				type: 'object'
			}
		},
		/**
		 * Defines the action metadata for the action button located on the footer of the card.
		 * @type {object}
		 */
		footerLinkAction: {
			default: {},
			schema: {
				type: 'object'
			}
		},
		/**
		 * Specifies the text located at the top of the card.
		 * This is the first of three available slots.
		 * @type {string}
		 */
		textHeaderLabelOne: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies the text located at the top of the card.
		 * This is the second of three available slots.
		 * @type {string}
		 */
		textHeaderLabelTwo: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies the text located at the top of the card.
		 * This is the third of three available slots.
		 * @type {string}
		 */
		textHeaderLabelThree: {
			default: '',
			schema: {
				type: 'string'
			}
		},

		/**
		 * Specifies a label to describe the click interaction with the card.
		 * @type {string}
		 */
		ariaLabel: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Specifies an object that contains data associated to the card.
		 * @type {string}
		 */
		model: {
			default: {},
			schema: {
				type: 'object'
			}
		}
	},
	actionHandlers: {
		[NOW_CARD_CLICKED]: ({
			dispatch,
			action: {
				payload: { action, model }
			}
		}) => {
			dispatch(DECLARATIVE_ACTION, {
				action,
				model: {
					resultType: REGULAR_RESULT_TYPE,
					...model
				},
				type: CLICK
			});
		},
		[NOW_BUTTON_BARE_CLICKED]: ({
			dispatch,
			action: {
				payload: { action, model }
			}
		}) => {
			dispatch(DECLARATIVE_ACTION, {
				action,
				model: {
					resultType: REGULAR_RESULT_TYPE,
					...model
				}
			});
		}
	},
	styles,
	behaviors: [{ behavior: rtlBehavior }]
});
