import '@servicenow/now-label-value';
import isEmpty from 'lodash/isEmpty';

const LABEL_TYPES = {
	STACKED: 'stacked',
	INLINE: 'inline'
};

const getSecondaryInformation = data =>
	data
		.map(content => {
			const item = Object.values(content);
			if (item[0] && item[1]) {
				return [
					{
						label: item[0],
						value: item[1]
					}
				];
			}
			return undefined;
		})
		.filter(item => item !== undefined);

const getStackedLabelElement = ({
	detailLabelOne,
	detailValueOne,
	detailLabelTwo,
	detailValueTwo
}) => {
	const secondaryInfo = getSecondaryInformation([
		{ detailLabelOne, detailValueOne },
		{ detailLabelTwo, detailValueTwo }
	]);
	const style = { maxWidth: `${100 / secondaryInfo.length}%` };

	const stackedLabels = secondaryInfo.map((value, index) => (
		<div style={style}>
			<now-label-value-stacked
				align="horizontal-equal"
				delimiter=","
				items={secondaryInfo[index]}
				size="sm"
				truncated
			/>
		</div>
	));

	return (
		<div className="label-value-section">
			<div className="label-value-section__stacked">{stackedLabels}</div>
		</div>
	);
};

const getInlineLabelElement = ({
	detailLabelSeparator,
	detailLabelOne,
	detailValueOne,
	detailLabelTwo,
	detailValueTwo
}) => {
	const useSeparator =
		(!isEmpty(detailLabelOne) || !isEmpty(detailValueOne)) &&
		(!isEmpty(detailLabelTwo) || !isEmpty(detailValueTwo)) &&
		!isEmpty(detailLabelSeparator);

	return (
		<div className="label-value-section">
			<div className="label-value-section__inline">
				{(detailLabelOne || detailValueOne) && (
					<now-label-value-inline
						label={detailLabelOne}
						value={detailValueOne}
						truncated
					/>
				)}
				{useSeparator ? (
					<now-label-value-inline
						className="label-value-section__separator"
						label={detailLabelSeparator}
						truncated
					/>
				) : null}
				{(detailLabelTwo || detailValueTwo) && (
					<now-label-value-inline
						label={detailLabelTwo}
						value={detailValueTwo}
						truncated
					/>
				)}
			</div>
		</div>
	);
};

const labelValue = ({
	detailLabelType,
	detailLabelSeparator,
	detailLabelOne,
	detailValueOne,
	detailLabelTwo,
	detailValueTwo
}) => {
	const labelType = isEmpty(detailLabelType)
		? LABEL_TYPES.INLINE
		: detailLabelType;

	let labelElement = null;
	if (labelType === LABEL_TYPES.STACKED) {
		labelElement = getStackedLabelElement({
			detailLabelOne,
			detailValueOne,
			detailLabelTwo,
			detailValueTwo
		});
	} else if (labelType === LABEL_TYPES.INLINE) {
		labelElement = getInlineLabelElement({
			detailLabelSeparator,
			detailLabelOne,
			detailValueOne,
			detailLabelTwo,
			detailValueTwo
		});
	}
	return labelElement;
};

export default labelValue;
