import '@servicenow/now-avatar';
import '@servicenow/now-icon';
import { AVATAR, ICON, IMAGE, DEFAULT_ICON } from '../constants';

const getImageElement = imageURL => {
	const imageOnError = e => {
		e.target.style.display = 'none';
	};

	if (imageURL && imageURL.length > 0) {
		return (
			<img
				on-error={imageOnError}
				src={imageURL}
				className="marker-image"
				alt="Card marker"
			/>
		);
	}
	return null;
};

const getIconElement = icon => (
	<now-icon icon={icon || DEFAULT_ICON} size="xl" />
);

const getAvatarElement = (avatarURL, avatarName) => (
	<now-avatar
		aria-hidden="true"
		image-src={avatarURL}
		size="md"
		user-name={avatarName}
	/>
);

const getDisplayElement = imageInfo => {
	const { imageType, imageURL, icon, avatarURL, avatarName } = imageInfo;
	let markerElement;

	switch (imageType) {
		case IMAGE:
			markerElement = getImageElement(imageURL);
			break;
		case ICON:
			markerElement = getIconElement(icon);
			break;
		case AVATAR:
			markerElement = getAvatarElement(avatarURL, avatarName);
			break;
		default:
			markerElement = getIconElement();
			break;
	}
	if (markerElement === null) markerElement = getIconElement(icon);
	return markerElement;
};

const marker = ({ imageType, imageURL, icon, avatarURL, avatarName }) => {
	const titleImageDisplay = {
		imageType,
		imageURL,
		icon,
		avatarURL,
		avatarName
	};
	const markerElement = getDisplayElement(titleImageDisplay);

	return <div className="main-content-marker">{markerElement}</div>;
};

export default marker;
