import has from 'lodash/has';
import isEmpty from 'lodash/isEmpty';

const button = ({ footerLinkAction, model }) => {
	if (
		isEmpty(footerLinkAction) ||
		isEmpty(model) ||
		!has(footerLinkAction, 'label')
	)
		return null;

	const footerLinkPayload = { action: footerLinkAction, model };

	return (
		<now-button-bare
			className="footer-content-section__button"
			label={footerLinkAction.label}
			size="sm"
			variant="secondary"
			append-to-payload={footerLinkPayload}
			hide-padding
		/>
	);
};

export default button;
