import '@servicenow/now-card';
import isEmpty from 'lodash/isEmpty';

import Identifier from './sections/identifier';
import Marker from './sections/marker';
import Header from './sections/header';
import Description from './sections/description';
import LabelValue from './sections/labelValue';
import Button from './sections/button';

const SearchResult = props => {
	const {
		textHeaderLabelOne,
		textHeaderLabelTwo,
		textHeaderLabelThree,
		detailLabelType,
		detailLabelSeparator,
		detailLabelOne,
		detailValueOne,
		detailLabelTwo,
		detailValueTwo,
		imageType,
		imageURL,
		icon,
		avatarURL,
		avatarName,
		title,
		ariaLabel,
		summary,
		clickAction,
		footerLinkAction,
		model
	} = props;

	const isActionsValid = !isEmpty(clickAction) && !isEmpty(model);
	const interaction = isActionsValid ? 'click' : 'none';
	const payload = isActionsValid ? { action: clickAction, model } : null;

	return (
		<now-card
			size="md"
			className="now-card-template"
			interaction={interaction}
			append-to-payload={payload}
			config-aria={{ 'aria-label': ariaLabel }}
		>
			<div className="now-card-container">
				<Marker
					imageType={imageType}
					imageURL={imageURL}
					icon={icon}
					avatarURL={avatarURL}
					avatarName={avatarName}
				/>
				<div className="header-content-section">
					<Identifier
						textHeaderLabelOne={textHeaderLabelOne}
						textHeaderLabelTwo={textHeaderLabelTwo}
						textHeaderLabelThree={textHeaderLabelThree}
					/>
					<div className="main-content">
						<Header title={title} />
						<Description summary={summary} />
					</div>
					<div className="footer-content-section">
						<LabelValue
							detailLabelType={detailLabelType}
							detailLabelSeparator={detailLabelSeparator}
							detailLabelOne={detailLabelOne}
							detailValueOne={detailValueOne}
							detailLabelTwo={detailLabelTwo}
							detailValueTwo={detailValueTwo}
						/>
						<Button footerLinkAction={footerLinkAction} model={model} />
					</div>
				</div>
			</div>
		</now-card>
	);
};

export default state => {
	const props = {
		...state.properties
	};

	return <SearchResult {...props} />;
};
