import { dangerouslyCreateElementFromString } from '@servicenow/ui-renderer-snabbdom';

const header = ({ title }) => {
	const titleText = `<div>${title}</div>`;
	const titleElement = dangerouslyCreateElementFromString(titleText);

	return (
		<div>
			<h3 className="main-content-title">{titleElement}</h3>
		</div>
	);
};

export default header;
