import isEmpty from 'lodash/isEmpty';
import filter from 'lodash/filter';
import flatMap from 'lodash/flatMap';

const getLabelElement = (text, className) => {
	if (isEmpty(text)) return null;
	return (
		<now-label-value-inline
			label={text}
			value=""
			className={className}
			truncated
		/>
	);
};

const identifier = ({
	textHeaderLabelOne,
	textHeaderLabelTwo,
	textHeaderLabelThree
}) => {
	const separator = '|';
	const identifierClass = 'header-section__identifier';
	const separatorClass = 'header-section__identifier-separator';

	const identifiers = filter(
		[
			getLabelElement(textHeaderLabelOne, identifierClass),
			getLabelElement(textHeaderLabelTwo, identifierClass),
			getLabelElement(textHeaderLabelThree, identifierClass)
		],
		x => x
	);

	const separatedIdentifiers = flatMap(identifiers, (value, index, array) =>
		array.length - 1 !== index
			? [value, getLabelElement(separator, separatorClass)]
			: value
	);

	const full = isEmpty(separatedIdentifiers) ? '' : '-full';

	return (
		<div className={`header-section ${full}`}>
			<div className="header-section__identifier-container">
				{separatedIdentifiers}
			</div>
		</div>
	);
};

export default identifier;
