import isEmpty from 'lodash/isEmpty';
import { dangerouslyCreateElementFromString } from '@servicenow/ui-renderer-snabbdom';

const description = ({ summary }) => {
	if (isEmpty(summary)) return null;

	const summaryText = `<div>${summary}</div>`;
	const summaryElement = dangerouslyCreateElementFromString(summaryText);

	return <div className="summaryText">{summaryElement}</div>;
};

export default description;
